@use "sass:math";

//   ===========================================================================================================================================================================================================================================================================
@import "base/mixins";

//   ===========================================================================================================================================================================================================================================================================
//&display=swap -     

//      
@import "fonts/fonts";

//    ==============================================================================================================================================================================================================================================================================
//      
//@import "fonts/icons";

//    ==============================================================================================================================================================================================================================================================================
$fontFamily: "Montserrat", sans-serif;
$second-family: "Inter", sans-serif;
$fontSize: 14px; //  14(px) -      

//  
$mainColor: #1e1e1e; //    
$black: #000000;
$white: #ffffff;
$blue: #1b53ff;
$darkblue: #272968;
$red: #eb1c25;

//  ===============================================================================================================================================================================================================================================================================================
//   
$minWidth: 320;
//   ()
$maxWidth: 1920;
//    (0 =  )
$maxWidthContainer: 1112;
//    
// (30 =  15px   , 0 =  )
$containerPadding: 30;

//    
$containerWidth: $maxWidthContainer + $containerPadding;

// -
$pc: 1142px; // , ,     
$tablet: 991.98px; // ,     
$mobile: 767.98px; //  L
$mobileSmall: 479.98px; //  S

//  :
// 1 =  (   )
$responsiveType: 1;

//     ============================================================================================================================================================================================== =============================================================================================== ===============================================================================================
@import "base/null";

body {
	//  
	.lock & {
		overflow: hidden;
		touch-action: none;
	}
	.filter-open & {
		overflow: hidden;
		touch-action: none;
	}
	//  
	.loaded & {
	}
}
//  
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	//  footer
	> main {
		flex: 1 1 auto;
	}
	//   
	> * {
		min-width: 0;
	}
}

.page {
	background: #f0f0f0;
}
//   ======================================================================================================================================================================================================================

/*
(i)     
   *__container
 header__container, main__container  ..
 (HTML): cnt
*/
@if ($responsiveType==1) {
	// 
	[class*="__container"] {
		@if ($maxWidthContainer>0) {
			max-width: rem($maxWidthContainer);
			box-sizing: content-box;
			margin: 0 auto;
		}
		@if ($containerPadding>0) {
			padding: 0 rem(math.div($containerPadding, 2));
		}
	}
}

//   ,  ()    
//  /    base.scss
@import "base";

//     
@import "common";

//    
@import "header";
@import "footer";

//    
@import "home";
@import "property";
@import "about";
@import "form-partners";
@import "purchase";
@import "partners";
@import "residential-complex";
@import "product-card";
